(function () {
    'use strict';

    angular
        .module('gsiteApp')
        .controller('MToolBarController', MToolBarController);

    MToolBarController.$inject = ['$scope','$state', '$mdSidenav', 'entity'];

    function MToolBarController($scope,$state, $mdSidenav, entity) {
        var vm = this;

        vm.homeState = $state.current.name;

        // prevent reload page => make change root state
        if (vm.homeState.indexOf('.') > 0) {
            vm.homeState = vm.homeState.split('.')[0];
        }
        vm.website = entity;
        vm.toolbar = vm.website.custom.toolbar;
        if (vm.toolbar == null)
            loadDefault();

        // sidenav
        vm.toggleLeft = buildToggler('m-sidenav');

        function buildToggler(componentId) {
            return function () {
                $mdSidenav(componentId).toggle();
            };
        }

        function loadDefault() {
            vm.toolbar = {
                isEnable: true,
                title: 'Person',
                textColor: '#FFFFFF'
            };

            vm.list = {
                isEnable: true,
                choices: [{
                        title: 'Information',
                        subTitle: 'Overview of what you should know',
                        icon: 'person',
                        state: vm.homeState + '.info'
                    },
                    {
                        title: 'Photos',
                        subTitle: 'Album contain all photo of reciever',
                        icon: 'photo_library',
                        state: vm.homeState + '.photo'
                    },
                    {
                        title: 'Songs',
                        subTitle: 'All favorite songs and their playlist',
                        icon: 'library_music',
                        state: vm.homeState + '.song'
                    }
                ]
            };
        }
    }
})();

(function () {
    'use strict';

    angular
        .module('gsiteApp')
        .controller('MSongDialogController', MSongDialogController);

    MSongDialogController.$inject = ['entity', 'Principal', 'MyWebsiteStorage', 'webId'];

    function MSongDialogController(entity, Principal, MyWebsiteStorage, webId) {
        var vm = this;
        vm.song = entity;

        vm.submit = submit;
        vm.deleteItem = deleteItem;
        var userLogin = null;
        vm.loading = false;
        vm.songFile = null;

        vm.item = {
            title: null,
            artist: null,
            url: null
        };

        vm.upload = upload;

        Principal.identity().then(function (user) {
            userLogin = user.login;
        });

        function reset() {
            vm.item = {
                title: null,
                artist: null,
                url: null
            };
            vm.songFile = null;
            vm.loading = false;
        }

        function submit(item) {
            if (!isExisted(item)) {
                uploadSong();
            }
        }

        function isExisted(item) {
            for (var i = 0; i < vm.song.items.length; i++) {
                var song = vm.song.items[i];
                if (song.title == item.title)
                    return true;
            }
            return false;
        }

        function deleteItem(index) {
            var fileName = vm.song.items[index].url;
            vm.loading = true;
            MyWebsiteStorage.deleteUserWebSong(userLogin, webId, fileName).then(onSuccess, onError);
            function onSuccess(response) {
                vm.song.items.splice(index, 1);
                reset();
            }

            function onError(response) {
                reset();
            }

        }

        function upload(file) {
            if (file != null) {
                vm.item.url = file.name;
                vm.item.title = file.name.split('.')[0];
            }
        }

        function uploadSong() {
            if (vm.songFile != null) {
                vm.loading = true;
                vm.song.items.push(vm.item);
                MyWebsiteStorage.uploadUserWebSong(userLogin, webId, vm.songFile, vm.item.url).then(onSuccess, onError);
            }

            function onSuccess(response) {
                reset();
            }

            function onError(response) {
                reset();
            }
        }
    }
})();

(function () {
    'use strict';
    angular
        .module('gsiteApp')
        .factory('MSongService', MSongService);

    MSongService.$inject = ['ngAudio', '$rootScope'];

    function MSongService(ngAudio, $rootScope) {
        var songList = null;
        var currentSong = null;
        var audio = null;
        var isPlaying = false;
        var currentIndex = 0;

        var instance = {
            loadSongList: loadSongList,
            getList: getList,
            loadSongAt: loadSongAt,
            play: play,
            stop: stop,
            pause: pause,
            playNext: playNext,
            playPrevious: playPrevious,
            subscribe: subscribe,
            checkPlaying: checkPlaying,
            getCurrentSong: getCurrentSong,
            getCurrentAudio: getCurrentAudio,
            setLoop: setLoop
        };


        function loadSongList(list) {
            songList = list;
        }

        function getList() {
            return songList;
        }

        function loadSongAt(index) {
            currentIndex = index;
            currentSong = songList[index];
            if (audio)
                stop();
            audio = ngAudio.load(currentSong.tempLink);
            return audio;
        }

        function play() {
            audio.play();
            isPlaying = true;
            notify();
        }

        function stop() {
            if (audio) {
                audio.stop();
                isPlaying = false;
                notify();
            }
        }

        function pause() {
            audio.pause();
            isPlaying = false;
            notify();
        }

        function playNext() {
            if (currentIndex + 1 < songList.length)
                loadSongAt(currentIndex + 1);
            else
                loadSongAt(0);

            play();
        }

        function playPrevious() {
            if (currentIndex - 1 >= 0)
                loadSongAt(currentIndex - 1);
            else
                loadSongAt(songList.length - 1);

            play();
        }

        function setLoop(isLoop) {
            audio.loop = isLoop;
        }

        function checkPlaying() {
            return isPlaying;
        }

        function getCurrentSong() {
            return currentSong;
        }

        function getCurrentAudio() {
            return audio;
        }

        function subscribe(scope, callback) {
            var handler = $rootScope.$on('notifying-service-event', callback);
            scope.$on('$destroy', handler);
        }

        function notify() {
            $rootScope.$emit('notifying-service-event');
        }

        return instance;
    }

})();

