(function () {
    'use strict';
    angular
        .module('gsiteApp')
        .factory('TemplateService', TemplateService);

    TemplateService.$inject = ['$rootScope','WebTemplate','TemplateStorage'];

    function TemplateService($rootScope,WebTemplate,TemplateStorage) {
        var instance = {
            all: all,
            subscribe: subscribe
        };

        var templates = [];

        loadAll();

        function all() {
            return templates;
        }

        function loadAll() {
            WebTemplate.query({}, onSuccess);
        }

        function onSuccess(data) {
            templates = data;
            loadImages(templates);
            notify();
        }

        function loadImages(webTemplates) {
            for (var i = 0; i < webTemplates.length; i++) {
                var template = webTemplates[i];
                if (template.image != null) {
                    TemplateStorage.loadImageForWebTemplateItem(template.id, template, "mainImage.jpg");
                }
            }
        }

        function subscribe(scope, callback) {
            var handler = $rootScope.$on('notifying-service-event', callback);
            scope.$on('$destroy', handler);
        }

        function notify() {
            $rootScope.$emit('notifying-service-event');
        }


        return instance;
    }
})();

(function () {
    'use strict';

    angular
        .module('gsiteApp')
        .controller('TemplateController', TemplateController);

    TemplateController.$inject = ['$scope', '$timeout','TemplateSearch', '$stateParams'];

    function TemplateController($scope, $timeout,TemplateSearch, $stateParams) {
        var vm = this;
        vm.currentPage = 0;

        vm.webTemplates = [];


        vm.currentSearch = 'name';
        vm.currentSort = 'name';
        vm.changeSearchType = changeSearchType;
        vm.changeSortType = changeSortType;

        loadTemplates();

        function loadTemplates() {
            vm.webTemplates = TemplateSearch.getTemplates();
        }

        TemplateSearch.subscribe($scope, function somethingChanged() {
            loadTemplates();
        });

        function changeSearchType() {
            TemplateSearch.setSearchType(vm.currentSearch);
        }

        function changeSortType() {
            TemplateSearch.setSortType(vm.currentSort);
        }

    }
})();

