                    'Authorization': 'Bearer ' + storageToken,
                    'Content-Type': 'application/octet-stream',
                    'Dropbox-API-Arg': '{"path": "/api/images/' + userId + '/' + webId + '/'  + fileName + '", "autorename": false,"mode":{".tag":"overwrite"}}'
